  width: 1200px;
  max-width: 100%;
  float: right;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
}
.comments__box:before, .comments__box:after {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  display: block;
  border-width: 10px 12px 10px 0;
  border-style: solid;
  border-color: transparent #fcfcfc;
  top: 8px;
  left: -11px;
}
.comments__box:before {
  border-width: 11px 13px 11px 0;
  border-color: transparent rgba(0, 0, 0, 0.05);
  left: -12px;
}
.comments__box__head {
  background: #fcfcfc;
  padding: 10px 12px;
  border-bottom: 1px solid #e5e5e5;
  overflow: hidden;
  border-radius: 4px 4px 0 0;
  display: flex;
  align-items: center;
}
.comments__box__head__icons {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.comments__box__head__icons i {
  margin-left: 14px;
  position: relative;
  top: 2px;
  color: #a6a6a6;
  cursor: pointer;
  transition: color 0.3s ease;
}
.comments__box__head__icons i:first-child {
  margin-left: 0;
}
.comments__box__head__icons i:hover {
  color: #03658c;
}
.comments__box__head__time {
  color: #999;
  font-size: 13px;
  position: relative;
  top: 1px;
}
.comments__box__name {
  color: #283035;
  font-size: 14px;
  font-weight: 700;
  float: left;
  margin-right: 10px;
}
.comments__box__name a {
  color: #283035;
}
.comments__box__name--author {
  color: #03658c;
}
.comments__box__name--author a {
  color: #03658c;
}
.comments__box__name--author:after {
  content: "author";
  background: #03658c;
  color: #fff;
  font-size: 12px;
  padding: 3px 5px;
  font-weight: 700;
  margin: 10px;
  border-radius: 3px;
}
.comments__box__content {
  background: #fff;
  padding: 12px;
  font-size: 15px;
  color: #595959;
  border-radius: 0 0 4px 4px;
}

.comments--reply {
  padding-left: 88px;
  clear: both;
  margin-top: 15px;
}
.comments--reply:before, .comments--reply:after {
  display: none;
}
.comments--reply li:before {
  content: "";
  width: 60px;
  height: 2px;
  background: #c7cacb;
  position: absolute;
  top: 25px;
  left: -55px;
}
.comments--reply li .comments__avatar .comments__avatar__person {
  width: 50px;
  height: 50px;
}
.comments--reply li .comments__box {
  width: 1100px;
  max-width: 100%;
}

