: 100%;\n    background: $detaile;\n    position: absolute;\n    left: 32px;\n    top: 0;\n  }\n\n  &:after {\n    content: \"\";\n    position: absolute;\n    background: $detaile;\n    bottom: 0;\n    left: 27px;\n    width: 7px;\n    height: 7px;\n    border: 3px solid $fondo;\n    border-radius: 50%;\n  }\n\n  li {\n    margin-bottom: 15px;\n    display: block;\n    position: relative;\n\n    &:after {\n      content: \"\";\n      display: block;\n      clear: both;\n      height: 0;\n      width: 0;\n    }\n  }\n\n  &__avatar {\n    width: 65px;\n    height: 65px;\n    position: relative;\n    background-image: url(../images/aiony-haust.jpg);\n    background-size: cover;\n    background-position: center;\n    z-index: 99;\n    float: left;\n    border: 3px solid #fff;\n    border-radius: 4px;\n    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);\n  }\n\n  &__avatar__person {\n    width: 65px;\n    height: 65px;\n    position: relative;\n    background-image: url(../images/christopher-campbell.jpg);\n    background-size: cover;\n    background-position: center;\n    background-size: cover;\n    background-position: center;\n    z-index: 99;\n    float: left;\n    border: 3px solid #fff;\n    border-radius: 4px;\n    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);\n  }\n\n  &__box {\n    width: 1200px;\n    max-width: 100%;\n    float: right;\n    position: relative;\n    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);\n\n    &:before,\n    &:after {\n      content: \"\";\n      height: 0;\n      width: 0;\n      position: absolute;\n      display: block;\n      border-width: 10px 12px 10px 0;\n      border-style: solid;\n      border-color: transparent #fcfcfc;\n      top: 8px;\n      left: -11px;\n    }\n\n    &:before {\n      border-width: 11px 13px 11px 0;\n      border-color: transparent rgba(0, 0, 0, 0.05);\n      left: -12px;\n    }\n\n    &__head {\n      background: #fcfcfc;\n      padding: 10px 12px;\n      border-bottom: 1px solid #e5e5e5;\n      overflow: hidden;\n      border-radius: 4px 4px 0 0;\n      display: flex;\n      align-items: center;\n\n      &__icons {\n        margin-left: auto;\n        display: flex;\n        align-items: center;\n\n        i {\n          margin-left: 14px;\n          position: relative;\n          top: 2px;\n          color: #a6a6a6;\n          cursor: pointer;\n          transition: color 0.3s ease;\n\n          &:first-child {\n            margin-left: 0;\n          }\n\n          &:hover {\n            color: $azul;\n          }\n        }\n      }\n\n      &__time {\n        color: #999;\n        font-size: 13px;\n        position: relative;\n        top: 1px;\n      }\n    }\n\n    &__name {\n      color: $oscuro;\n      font-size: 14px;\n      font-weight: 700;\n      float: left;\n      margin-right: 10px;\n\n      a {\n        color: $oscuro;\n      }\n\n      &--author {\n        color: $azul;\n\n        a {\n          color: $azul;\n        }\n\n        &:after {\n          content: \"author\";\n          background: $azul;\n          color: #fff;\n          font-size: 12px;\n          padding: 3px 5px;\n          font-weight: 700;\n          margin: 10px;\n          border-radius: 3px;\n        }\n      }\n    }\n\n    &__content {\n      background: #fff;\n      padding: 12px;\n      font-size: 15px;\n      color: #595959;\n      border-radius: 0 0 4px 4px;\n    }\n  }\n}\n\n.comments--reply {\n  padding-left: 88px;\n  clear: both;\n  margin-top: 15px;\n\n  &:before,\n  &:after {\n    display: none;\n  }\n\n  li {\n    &:before {\n      content: \"\";\n      width: 60px;\n      height: 2px;\n      background: $detaile;\n      position: absolute;\n      top: 25px;\n      left: -55px;\n    }\n\n    .comments__avatar .comments__avatar__person {\n      width: 50px;\n      height: 50px;\n    }\n\n    .comments__box {\n      width: 1100px;\n      max-width: 100%;\n    }\n  }\n}\n\n@media only screen and (max-width: 766px) {\n  .comments-container {\n    width: 480px;\n    max-width: 100%;