x;\n    width: 100%;\n    font-size: 0.9rem;\n\n    a {\n      text-decoration: dotted underline;\n    }\n\n    ul li {\n      display: inline-block;\n    }\n\n    &--author:before {\n      font: var(--fa-font-solid);\n      margin-right: 10px;\n      content: \"\\f007\";\n    }\n\n    &--date:before {\n      font: var(--fa-font-solid);\n      margin-right: 10px;\n      content: \"\\f133\";\n    }\n\n    &--tags {\n      ul:before {\n        font: var(--fa-font-solid);\n        content: \"\\f02b\";\n        margin-right: 10px;\n      }\n\n      li {\n        margin-right: 2px;\n\n        &:first-child {\n          margin-left: -4px;\n        }\n      }\n    }\n  }\n\n  &__description {\n    padding: 1rem;\n    background: $color_white;\n    position: relative;\n    z-index: 1;\n\n    h1,\n    h2 {\n      font-family: \"Fira Sans\", sans-serif;\n    }\n\n    &--title {\n      line-height: 1;\n      margin: 0;\n      font-size: 1.7rem;\n    }\n\n    &--subtitle {\n      font-size: 1rem;\n      font-weight: 300;\n      text-transform: uppercase;\n      color: $color_grey_dark;\n      margin-top: 5px;\n    }\n\n    &--read-more {\n      text-align: right;\n\n      a {\n        color: $color_prime;\n        display: inline-block;\n        position: relative;\n\n        &:after {\n          content: \"\\f061\";\n          font-weight: 900;\n          font: var(--fa-font-solid);\n          margin-left: -10px;\n          opacity: 0;\n          vertical-align: middle;\n          transition:\n            margin 0.3s,\n            opacity 0.3s;\n        }\n\n        &:hover:after {\n          margin-left: 5px;\n          opacity: 1;\n        }\n      }\n    }\n  }\n\n  p {\n    position: relative;\n    margin: 1rem 0 0;\n\n    &:first-of-type {\n      margin-top: 1.25rem;\n\n      &:before {\n        content: \"\";\n        position: absolute;\n        height: 5px;\n        background: $color_prime;\n        width: 35px;\n        top: -0.75rem;\n        border-radius: 3px;\n      }\n    }\n  }\n\n  &:hover {\n    .blog-card__details {\n      left: 0%;\n    }\n  }\n\n  @media (min-width: 640px) {\n    flex-direction: row;\n    max-width: 700px;\n\n    .blog-card__meta {\n      flex-basis: 40%;\n      height: auto;\n    }\n\n    .blog-card__description {\n      flex-basis: 60%;\n\n      &:before {\n        content: \"\";\n        background: #fff;\n        width: 30px;\n        position: absolute;\n        left: -10px;\n        top: 0;\n        bottom: 0;\n        z-index: -1;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
